import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HttpModule } from '@angular/http';

import { TemplateEditorComponent } from './template-editor.component';
import { TemplateEditorRouting } from './template-editor.routing';

import { TemplateService } from '@services/httpServices/template.service';


import { AppTemplateEditorNewsletterComponent } from '@global_components/app-template-editor-newsletter/app-template-editor-newsletter.component';

const APP_COMPONENTS = [
  AppTemplateEditorNewsletterComponent
];


const TEMPLATE_EDITOR_MODULE_COMPONENTS = [];


@NgModule({
  imports: [
    CommonModule,
    HttpModule,
    TemplateEditorRouting
  ],
  declarations: [
    TemplateEditorComponent,
    ...APP_COMPONENTS,
    ...TEMPLATE_EDITOR_MODULE_COMPONENTS
  ],
  providers: [
    TemplateService
  ]
})

export class TemplateEditorModule { }



