import { Component, OnInit, Input, AfterViewInit } from '@angular/core';

declare var grapesjs: any;

@Component({
  selector: 'app-template-editor-newsletter',
  templateUrl: './app-template-editor-newsletter.component.html',
  styleUrls: ['./app-template-editor-newsletter.component.scss']
})

export class AppTemplateEditorNewsletterComponent implements OnInit {
  @Input() htmlInput: string;
  editor: any;
  constructor() {}
  ngOnInit () {
    this.editor = grapesjs.init({
      container : '#gjs',
      components: '<div class="txt-red">Hello world!</div>',
      style: '.txt-red{color: red}',
      plugins: ['gjs-preset-newsletter'],
    });
    this.editor.Panels.addButton('options', [{
      id: 'save',
      className: 'fa fa-floppy-o icon-blank',
      command: function (editor, sender) {
        if (sender) {
          sender.set('active', 0);
        }
        const commandGetHTML = editor.Commands.get('gjs-get-inlined-html');
        const html_output = commandGetHTML.run(editor);
        console.log(html_output)
        // TODO
      },

      attributes: {title: 'Save Template'}
    }]);
  }
}



