import { NgModule } from '@angular/core';
import {
  Routes,
  RouterModule
} from '@angular/router';

import { TemplateEditorComponent } from './template-editor.component';
import {AppTemplateEditorNewsletterComponent} from '@global_components/app-template-editor-newsletter/app-template-editor-newsletter.component';

const routes: Routes = [
  {
    path: '',
    component: TemplateEditorComponent,
    data: {
      title: 'Mail'
    },
    children: [
      {
        path: 'mail',
        component: AppTemplateEditorNewsletterComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TemplateEditorRouting {}



